body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  max-width: 100vw;
  min-height: 100vh;
  max-height: 100vh;
  background-color: black;
  scrollbar-arrow-color: red;
  color: white;
}
#mobileAlert{
  position: absolute;
  background-color: purple;
  top: 10vh;
  z-index: 1;
  -webkit-animation: fadeout 100ms linear 3000ms 1 forwards;
          animation: fadeout 100ms linear 3000ms 1 forwards;
  padding: 0 20px;
}
#headerContainer{
  padding-left: 20px;
  height: 8vh;
}
.headerTitle{
  font-size: 1.6rem;
  font-family: cursive;
  opacity: 0;
}

span:nth-child(1), span:nth-child(6), span:nth-child(12){
  font-size: 2em;
}

#headerContainer span{
  color: #ce4c4c;
}
