iframe{
  height: 25vh;
  width: 100vw;
}
img{
  height: 67vh;
  width: auto;
}
#hidden{
  display: none;
}
[id^="text"]{
  font-family: cursive;
  color: purple;
  font-size: 3rem;
  padding: 20px;
  transition: ease-in-out;

}
[id*="iloveyou"]{
  color: red;
  -webkit-animation: fadeout 100ms linear 5000ms 1 forwards;
          animation: fadeout 100ms linear 5000ms 1 forwards;

}
#pachabel{
  position: absolute;
  left: 100vw;
  
}
#winky-face{
  width: 100%;
  max-width: 300px;
  height: auto;
}

@-webkit-keyframes disappear{
    0%{
      display: inline-block;
    }
    100%{
      display: none;
    }
  }
@keyframes disappear{
    0%{
      display: inline-block;
    }
    100%{
      display: none;
    }
  }
@-webkit-keyframes slideleft{
    0%{
      top: 0px;
      left: 120vw;
      opacity: 1;
    }
    50%{
      top: 50vh;
      left: 50vw;
      opacity: 1;
    }
    100%{
      top: 0px;
      left: -80vw;
      opacity: 0;
    }
  }
@keyframes slideleft{
    0%{
      top: 0px;
      left: 120vw;
      opacity: 1;
    }
    50%{
      top: 50vh;
      left: 50vw;
      opacity: 1;
    }
    100%{
      top: 0px;
      left: -80vw;
      opacity: 0;
    }
  }
  @-webkit-keyframes fadeout{
    0%{
      opacity: 1;
    }
    100%{
      opacity: 0;
    }
  }
  @keyframes fadeout{
    0%{
      opacity: 1;
    }
    100%{
      opacity: 0;
    }
  }
  @-webkit-keyframes fadein{
    0%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
  }
  @keyframes fadein{
    0%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
  }
  @-webkit-keyframes slideright{
    0%{
      top: 0px;
      right: 120vw;
      opacity: 1;
    }
    50%{
      top: 50vh;
      right: 50vw;
      opacity: 1;
    }
    100%{
      top: 0px;
      right: -80vw;
      opacity: 0;
    }
  }
  @keyframes slideright{
    0%{
      top: 0px;
      right: 120vw;
      opacity: 1;
    }
    50%{
      top: 50vh;
      right: 50vw;
      opacity: 1;
    }
    100%{
      top: 0px;
      right: -80vw;
      opacity: 0;
    }
  }
